.darkScreen{
    background-color: #00000099;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    transition: 0.5s;

    .modalContent {
        background-color: var(--white);
        width: 700px;
        height: 400px;
        display: flex;
        border-radius: 6px;
        box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066;
    
        .modalSec45 {
            width: 45%;
            height: 100%;

            img {
                width: 100%;
                height: 100.2%;
                border-top-left-radius: 6px;
                border-bottom-left-radius: 6px;
                clip-path: polygon(0 0, 100% 0%, 75% 100%, 0% 100%);
                -webkit-touch-callout: none;
                -webkit-user-select: none;
                -khtml-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
            }
        }
        
        .modalSec55 {
            box-sizing: border-box;
            width: 55%;
            height: 100%;
            padding: 40px;
            display: flex;
            flex-direction: column;
            
            .modalHeader {
                width: 100%;
                height: 15%;
                margin: 0;
                padding: 0;
                display: flex;
                flex-direction: row;
                align-items: flex-start;
                justify-content: space-between;

                .modalTitle {
                    font-size: 1.2rem;
                    color: var(--title);
                }

                .closeButton {
                    width: 30px;
                    height: 30px;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    font-size: 1.2rem;
                    color: var(--title);
                    background-color: transparent;
                    border: none;
                    cursor: pointer;
                }
            }

            .modalMain {
                width: 100%;
                height: 85%;
                display: flex;
                flex-direction: column;

                .iconSec {
                    height: 20%;               

                    div {
                        margin-top: 4px;
                        color: var(--subtitle);
                        font-size: 0.8rem;   
                        display: flex;
                        align-items: center;

                        .modalIcon {
                            color: var(--red1);
                            margin-right: 8px;
                        }
                    }
                }

                .contentSec {
                    margin-top: 16px;
                    font-size: 0.8rem;
                    height: 80%;
                    text-align: justify;
                    overflow-y: auto;
                    scrollbar-width: thin;
                    scrollbar-color: var(--glassBg50);

                    .p {
                        color: var(--text);
                        text-indent: 24px;
                        margin-bottom: 16px;
                        margin-right: 16px;
                    }
                    .p:last-child {
                        margin-bottom: 0px;
                    }
                }
    
                .contentSec::-webkit-scrollbar {
                    width: 4px;
                }
                
                .contentSec::-webkit-scrollbar-track {
                    background: transparent;
                }
                
                .contentSec::-webkit-scrollbar-thumb {
                    background-color: var(--title);
                    border-radius: 20px;
                }

            }
        }
    }
}

.openModal{
    visibility: visible !important;
    opacity: 1 !important;
}

